header{
    .main-nav {
        left: 0%;
        top: 0;
        transform: translateX(0%);
        width: 100%;
        padding: 2.5px 0;
        z-index: 9999;
        @include tablet{
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            width: 100%;
            padding: 20px 0;
            z-index: 9999; 
        }
        .navbar-brand{
            .logo-color{
                fill:$white-color;
            }
        }
        .burger-menu{
            i{
                color: #fff;
            }

        }
        &.normal-nav{
            position: relative;
        }
    }
    ul.navbar-nav > li{
        
        @include tablet{
            margin: 10px 6px;
        }
        a{
            color: $primary-color;
            font:600 15px 'Open Sans', sans-serif;
            @include tablet{
                color: $white;
            }
            &:hover{
                color: $primary-color;
            }
        }
        
    }
}
.navbar-sticky{
    height: 80px;
    .logo-color{
        fill:$primary-color;
    }
    ul.navbar-nav{
        >li{
            @include tablet{
                margin: 10px 9px;
            }
            @include desktops{
                margin: 10px 5px;
            }
            >a{
                font:600 15px 'Open Sans', sans-serif;
            }
            ul{
                margin:0;
            }
        }
        
    }
}

.nav-search{
    
    @include tablet{
        width: 15px;
        text-align: center;
    }
    .nav-link{
        .fa-close {
            display: none;
        }
        &.search-trigger{
    
        //    @include tablet{
        //     display: block;
        //     margin: 0!important;
            
        //    }
        }
    }

.search_hook.dropdown-menu{
    display: block!important;
    padding:0px 10px 0px 0px;
    border-left: none;
    min-width: 100%;
    @include tablet{
        padding: 0;
        left: 0;
    }
	form.search_form{
        display: block;
		@include tablet{
			display: none;
		}
	}
}
	&.show{
        @include tablet{
        .nav-link{
        >.fa-search {
            display: none;
        }
        .fa-close {
            display: block;
            position: relative;
            top: 3px;
        }
        }
        }
        .dropdown-menu{
                width: 100%;
                z-index: -1;
        }
        form.search_form{
            @include tablet{
                display: block!important;
            }
        }
	}
}